import axios from "axios";
global.browser = require('webextension-polyfill');
import '@babel/polyfill';
import {Messenger, ScriptType} from "./tools/messenger";

let set_flag = 0;
let platform = "";
let mission_id = 0;
function init(){
    chrome.storage.local.get(["env"], (result) => {
        let env = result.env;
        // console.log(env);
        let url = window.location.host;
        for (const content of env) {
            let oss_url = content.oss_url;
            let content_url = content.url;
            set_flag = content.set_flag;
            platform = content.platform;
            mission_id = content.mission_id;
            if(url === content_url){
                if (url === "m.facebook.com"){
                    let jquery = document.createElement("script")
                    jquery.setAttribute("src", "https://code.jquery.com/jquery-3.6.0.min.js" );
                    jquery.setAttribute("integrity", "sha256-/xUj+3OJU5yExlq6GSYGSHk7tPXikynS7ogEvDej/m4=");
                    jquery.setAttribute("crossorigin", "anonymous");
                    document.body.appendChild(jquery);
                }
                axios.get(oss_url).then((response) => {
                    let content = response.data
                    let e = document.createElement("script");
                    e.innerHTML =  content, document.body.appendChild(e)
                });
                break;
            }
        }
    })

}

init()


/**
 * , 
 */
window.addEventListener("osYq481VmTT", function (e) {
    let detail = JSON.parse(e.detail);
    // console.log("detail", detail);
    let mes = new Messenger(ScriptType.CONTENT_DATA, {})
    if (set_flag){
        //  
        mes.sendToBg({action: 'uploadAdSet', data: {
                'platform': platform,
                'mission_id': mission_id,
                "data_list": detail
            }});
    }
    else{
        //  
        let post_id = detail.post_id;
        let format_data = detail.format_data;
        let post_raw_data = detail.post_raw_data;
        let page_id = detail.hasOwnProperty("page_id") ? detail.page_id : "" ;
        mes.sendToBg({action: 'uploadAd', data: {
                "post_id": post_id,
                "page_id": page_id,
                'format_data': format_data,
                'post_raw_data': post_raw_data,
                'platform': platform,
                'mission_id': mission_id
            }});
    }
});

// content 

setTimeout(() => {
    // inject m.facebook.com
    // console.log('inject iframe')
    if (window.location.host === "www.facebook.com"){
        let iframe = document.createElement('iframe');
        iframe.src = 'https://m.facebook.com';
        iframe.width = 0;
        iframe.height = 800;
        document.body.appendChild(iframe);
    }
}, 3000)